"use client";
import { fetchData } from '@/app/_lib/productReducer';
import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Image from 'next/image';
import { Star, ShoppingCart, Bolt } from 'lucide-react';

export default function Product({ productId = 1 }) {
    const dispatch = useDispatch();
    const { data = [], loading, error } = useSelector(state => state.product);
    const product = data?.data;

    useEffect(() => {
        dispatch(fetchData(`/products/getProductById?productId=${productId}`));
    }, [dispatch, productId]);

    if (loading) return <p className="text-center text-lg font-medium">Loading...</p>;
    if (error) return <p className="text-center text-red-500">Error: {error}</p>;
    if (!product) return <p className="text-center text-gray-600">No product found</p>;

    return (
        <div className="container mx-auto p-4">
            <div className="flex flex-col lg:flex-row bg-white shadow-lg rounded-xl overflow-hidden">
                {/* Left - Image Section */}
                <div className="lg:w-2/5 p-4 flex justify-center items-center bg-gray-100">
                    <Image
                        src={product.image}
                        alt={product.name}
                        width={400}
                        height={300}
                        className="object-contain w-full h-auto max-w-xs rounded-lg"
                    />
                </div>

                {/* Right - Details Section */}
                <div className="lg:w-3/5 p-6 flex flex-col gap-4">
                    <h1 className="text-2xl font-bold text-gray-800">{product.name}</h1>

                    {/* Rating */}
                    <div className="flex items-center gap-2">
                        <span className="bg-orange-500 text-white px-2 py-1 rounded text-sm font-semibold flex items-center">
                            {product.rating} <Star className="ml-1 w-4 h-4" />
                        </span>
                        <span className="text-gray-500">({product.reviews} Reviews)</span>
                    </div>

                    {/* Price Section */}
                    <div className="flex items-center gap-3">
                        <span className="text-3xl font-bold text-gray-800">₹{product.offerPrice}</span>
                        <span className="text-gray-500 line-through">₹{product.actualPrice}</span>
                        <span className="text-orange-500 font-semibold">{product.discountPercentage}% off</span>
                    </div>

                    {/* Available Offers */}
                    <div className="p-4 border rounded-md bg-gray-100">
                        <h2 className="font-semibold text-gray-700">Available Offers</h2>
                        <ul className="list-disc pl-5 text-sm text-gray-600">
                            <li>10% instant discount on XYZ Bank Cards</li>
                            <li>No-cost EMI on selected credit cards</li>
                            <li>Exchange Offer: Get up to ₹5,000 off</li>
                        </ul>
                    </div>

                    {/* Description */}
                    <p className="text-gray-600 text-sm">{product.description}</p>

                    {/* Reviews Section */}
                    <div className="p-4 border rounded-md bg-gray-100">
                        <h2 className="font-semibold text-gray-700">Customer Reviews</h2>
                        <p className="text-sm text-gray-600">⭐⭐⭐⭐⭐ Amazing product!</p>
                        <p className="text-sm text-gray-600">⭐⭐⭐⭐ Great value for money.</p>
                    </div>

                    {/* Actions */}
                    <div className="flex flex-col sm:flex-row gap-3">
                        <button className="bg-gray-800 text-white px-6 py-2 rounded flex items-center justify-center gap-2 text-lg font-medium w-full sm:w-auto">
                            <ShoppingCart className="w-5 h-5" /> Add to Cart
                        </button>
                        <button className="bg-orange-500 text-white px-6 py-2 rounded flex items-center justify-center gap-2 text-lg font-medium w-full sm:w-auto">
                            <Bolt className="w-5 h-5" /> Buy Now
                        </button>
                    </div>
                </div>
            </div>
            <SimilarProducts subCategoryId={1}/>

        </div>
    );
}
